.Timer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: rgba(11, 7, 34, 0.9);
  position: absolute;
  width: 100vw;
  top: 0;
  left: 0;
  z-index: var(--layer-1);
}
.Timer[data-active="false"] {
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh;
  overflow: hidden;
}
.Timer[data-active="true"] {
  -ms-flex-align: start;
      align-items: flex-start;
  background: transparent;
  padding: .4em;
  width: 100vw;
}
.Timer__cluster {
  display: -ms-flexbox;
  display: flex;
  margin-top: 1rem;
}
.Timer__numeral {
  font-size: 4rem;
}
.Timer[data-active="true"] .Timer__regress-bar{
  height: 10px;
  background: var(--green);
  text-align: left;
  -webkit-transition: all 500ms ease-in;
  -o-transition: all 500ms ease-in;
  transition: all 500ms ease-in;
}
.Timer .button__play{
  font-size: 5rem;
  padding: 0;
  margin: 0;
  height: 9rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 9rem;
  -ms-flex-pack: center;
      justify-content: center;
  background: darkcyan;
  border: none;
  color: white;
  border-radius: 100%;
  margin-top: 2rem;
  font-family: Arial, Helvetica, sans-serif;
}

.Timer .button__pause {
  background: darkcyan;
  color: white;
  font-size: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin: .4rem 2rem 0 0;
  border: 0;
  border-radius: 100%;
  height: 4rem;
  width: 4rem;
  font-family: Arial, Helvetica, sans-serif;
}


