/* Button Reset and Custom */
.button {
  background: var(--button-gradient-active);
  font-size: 2.6rem;
  font-weight: bold;
  border-radius: 100%;
  border: none;
  color: var(--white);
  cursor: pointer;
  -webkit-transition: var(--transition);
  -o-transition: var(--transition);
  transition: var(--transition);
  outline: none;
}
button:focus,
button:active,
button:hover {
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
button::-moz-focusring,button::-moz-focus-inner{
  outline: none;
  border-style: 0;
}

.button[disabled] {
  opacity: 0.8;
  -webkit-transition: var(--transition);
  -o-transition: var(--transition);
  transition: var(--transition);
  background: var(--button-gradient-disabled);
  cursor: not-allowed;
}
.button[inactive] {
  opacity: 0.2;
  -webkit-transition: var(--transition);
  -o-transition: var(--transition);
  transition: var(--transition);
  -webkit-transform: rotate(720deg);
      -ms-transform: rotate(720deg);
          transform: rotate(720deg);
  background: var(--button-gradient-inactive);
  cursor: not-allowed;
}


